import React, { useEffect, useRef, useState } from 'react';
import { 
  Dialog, 
  DialogContent, 
  DialogHeader, 
  DialogTitle, 
  DialogFooter 
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Slider } from '@/components/ui/slider';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { useWorkoutLogStore } from '@/store/workoutLogStore';
import { useExercisesStore } from '@/store/exercisesStore';
import { cn } from '@/lib/utils';
import { WorkoutLogEntry } from '../../types/Workout_Log';

// Form schema that matches WorkoutLogEntry type (minus id field)
const formSchema = z.object({
  date: z.string().min(1, "Date is required"),
  muscleGroup: z.string().min(1, "Muscle group is required"),
  exercise: z.string().min(1, "Exercise is required"),
  reps: z.coerce.number().min(1, "Reps must be at least 1"),
  weight: z.coerce.number().min(0, "Weight must be non-negative"),
  rating: z.coerce.number().min(1, "Rating must be between 1-10").max(10, "Rating must be between 1-10"),
  restTime: z.coerce.number().min(0, "Rest time must be non-negative").nullable(),
  rowIndex: z.number().optional(),
  isSynced: z.boolean().optional().default(false),
  pendingAction: z.enum(['add', 'update', 'delete']).nullable().optional().default('add')
});

// Define form types that match WorkoutLogEntry
type FormSchema = z.infer<typeof formSchema>;
type FormData = Omit<WorkoutLogEntry, 'id'>;

interface AddWorkoutLogModalProps {
  isOpen: boolean;
  onClose: () => void;
  editEntry?: WorkoutLogEntry | Partial<WorkoutLogEntry> | null;
}

// Autocomplete suggestion component
interface AutocompleteInputProps {
  value: string;
  onChange: (value: string) => void;
  suggestions: string[];
  placeholder: string;
  className?: string;
}

const AutocompleteInput: React.FC<AutocompleteInputProps> = ({
  value,
  onChange,
  suggestions,
  placeholder,
  className
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [filteredSuggestions, setFilteredSuggestions] = useState<string[]>([]);
  const [highlightedIndex, setHighlightedIndex] = useState(-1);
  const inputRef = useRef<HTMLInputElement>(null);
  const listRef = useRef<HTMLUListElement>(null);

  useEffect(() => {
    if (value) {
      const filtered = suggestions.filter(suggestion =>
        suggestion.toLowerCase().includes(value.toLowerCase())
      );
      setFilteredSuggestions(filtered);
      setIsOpen(filtered.length > 0);
    } else {
      setFilteredSuggestions([]);
      setIsOpen(false);
    }
    setHighlightedIndex(-1);
  }, [value, suggestions]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    onChange(e.target.value);
  };

  const handleSuggestionClick = (suggestion: string) => {
    onChange(suggestion);
    setIsOpen(false);
    inputRef.current?.blur();
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (!isOpen) return;

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault();
        setHighlightedIndex(prev => 
          prev < filteredSuggestions.length - 1 ? prev + 1 : prev
        );
        break;
      case 'ArrowUp':
        e.preventDefault();
        setHighlightedIndex(prev => prev > 0 ? prev - 1 : -1);
        break;
      case 'Enter':
        e.preventDefault();
        if (highlightedIndex >= 0) {
          handleSuggestionClick(filteredSuggestions[highlightedIndex]);
        } else {
          setIsOpen(false);
        }
        break;
      case 'Escape':
        setIsOpen(false);
        inputRef.current?.blur();
        break;
    }
  };

  const handleBlur = () => {
    // Delay hiding to allow for clicks on suggestions
    setTimeout(() => setIsOpen(false), 150);
  };

  const handleFocus = () => {
    if (value && filteredSuggestions.length > 0) {
      setIsOpen(true);
    }
  };
  return (
    <div className="relative">
      <Input
        ref={inputRef}
        value={value}
        onChange={handleInputChange}
        onKeyDown={handleKeyDown}
        onBlur={handleBlur}
        onFocus={handleFocus}
        placeholder={placeholder}
        className={className}
      />
      {isOpen && filteredSuggestions.length > 0 && (
        <ul
          ref={listRef}
          className="absolute z-50 w-full mt-1 bg-background border border-border rounded-md shadow-lg max-h-60 overflow-auto"
        >
          {filteredSuggestions.map((suggestion, index) => (
            <li
              key={suggestion}
              onClick={() => handleSuggestionClick(suggestion)}
              className={cn(
                "px-3 py-2 cursor-pointer text-foreground hover:bg-muted transition-colors",
                highlightedIndex === index && "bg-muted"
              )}
            >
              {suggestion}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

const AddWorkoutLogModal: React.FC<AddWorkoutLogModalProps> = ({ 
  isOpen, 
  onClose, 
  editEntry = null 
}) => {
  // Handle modal close with cleanup
  const handleClose = () => {
    form.reset({
      date: new Date().toISOString().split('T')[0],
      muscleGroup: '',
      exercise: '',
      reps: 1,
      weight: 0,
      rating: 5,
      restTime: null,
      rowIndex: undefined,
      isSynced: false,
      pendingAction: 'add'
    });
    onClose();
  };
  const { addWorkoutLog, editWorkoutLog } = useWorkoutLogStore();  const { 
    exerciseGroups, 
    fetchExercises, 
    getExercisesByMuscleGroup, 
    getMuscleGroupsByExercise,
    getAllUniqueExerciseNames
  } = useExercisesStore();

  const form = useForm<FormSchema>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      date: new Date().toISOString().split('T')[0],
      muscleGroup: '',
      exercise: '',
      reps: 1,
      weight: 0,
      rating: 5,
      restTime: null,
      rowIndex: undefined,
      isSynced: false,
      pendingAction: 'add'
    },
  });

  // Fetch exercises data when modal opens
  useEffect(() => {
    if (isOpen && exerciseGroups.length === 0) {
      fetchExercises();
    }
  }, [isOpen, exerciseGroups.length, fetchExercises]);

  // Handle edit mode
  useEffect(() => {
    if (editEntry) {
      form.reset({
        date: editEntry.date,
        muscleGroup: editEntry.muscleGroup,
        exercise: editEntry.exercise,
        reps: editEntry.reps,
        weight: editEntry.weight,
        rating: editEntry.rating,
        restTime: editEntry.restTime,
      });
    } else {
      form.reset({
        muscleGroup: '',
        exercise: '',
        reps: 1,
        weight: 0,
        rating: 5,
        restTime: null,
        date: new Date().toISOString().split('T')[0],
      });
    }
  }, [editEntry, form]);

  // Get dynamic options based on current selections
  const currentMuscleGroup = form.watch('muscleGroup');
  const currentExercise = form.watch('exercise');

  // Get muscle groups from API data
  const availableMuscleGroups = exerciseGroups.map(group => group.muscleGroup);

  // Get exercises based on current muscle group selection or all exercises
  const availableExercises = currentMuscleGroup 
    ? getExercisesByMuscleGroup(currentMuscleGroup)
    : getAllUniqueExerciseNames();

  // Handle muscle group change - cross-selection logic
  const handleMuscleGroupChange = (muscleGroup: string) => {
    form.setValue('muscleGroup', muscleGroup);
    
    // If current exercise doesn't belong to selected muscle group, clear it
    if (currentExercise) {
      const exerciseMuscleGroups = getMuscleGroupsByExercise(currentExercise);
      if (!exerciseMuscleGroups.includes(muscleGroup)) {
        form.setValue('exercise', '');
      }
    }
  };

  // Handle exercise change - cross-selection logic
  const handleExerciseChange = (exercise: string) => {
    form.setValue('exercise', exercise);
    
    // If no muscle group selected or current muscle group doesn't match exercise, update it
    if (!currentMuscleGroup) {
      const exerciseMuscleGroups = getMuscleGroupsByExercise(exercise);
      if (exerciseMuscleGroups.length > 0) {
        form.setValue('muscleGroup', exerciseMuscleGroups[0]);
      }
    } else {
      const exerciseMuscleGroups = getMuscleGroupsByExercise(exercise);
      if (!exerciseMuscleGroups.includes(currentMuscleGroup)) {
        if (exerciseMuscleGroups.length > 0) {
          form.setValue('muscleGroup', exerciseMuscleGroups[0]);
        }
      }
    }
  };
  const onSubmit = async (data: FormData) => {
    try {
      // Create base entry data
      const baseEntry = {
        date: data.date,
        muscleGroup: data.muscleGroup,
        exercise: data.exercise,
        reps: data.reps,
        weight: data.weight,
        rating: data.rating,
        restTime: data.restTime,
      };

      if (editEntry && 'id' in editEntry) {
        // If editEntry is a full WorkoutLogEntry, update it
        const updatedEntry: WorkoutLogEntry = {
          ...editEntry, // This has the id
          ...baseEntry,
        };
        await editWorkoutLog(updatedEntry);
      } else {
        // For new entries or partial entries, create new
        await addWorkoutLog(baseEntry);
      }
      form.reset();
      onClose();
    } catch (error) {
      console.error('Failed to save workout log:', error);
    }
  };

  return (
    <Dialog 
      open={isOpen} 
      onOpenChange={(open) => {
        if (!open) {
          handleClose();
        }
      }}
    >
      <DialogContent className="sm:max-w-[600px] max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>
            {editEntry ? 'Edit Workout Log' : 'Add New Workout Log'}
          </DialogTitle>
        </DialogHeader>
        
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <FormField
                control={form.control}
                name="date"
                render={({ field }) => (
                  <FormItem className="col-span-full">
                    <FormLabel>Date</FormLabel>
                    <FormControl>
                      <Input type="date" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
                <FormField
                control={form.control}
                name="muscleGroup"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Muscle Group</FormLabel>
                    <FormControl>
                      <AutocompleteInput
                        value={field.value}
                        onChange={handleMuscleGroupChange}
                        suggestions={availableMuscleGroups}
                        placeholder="Type or select muscle group..."
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              
              <FormField
                control={form.control}
                name="exercise"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Exercise</FormLabel>
                    <FormControl>
                      <AutocompleteInput
                        value={field.value}
                        onChange={handleExerciseChange}
                        suggestions={availableExercises}
                        placeholder="Type or select exercise..."
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
                <FormField
                control={form.control}
                name="reps"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Reps</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        min="1"
                        max="1000"
                        {...field}
                        onChange={e => field.onChange(Number(e.target.value))}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              
              <FormField
                control={form.control}
                name="weight"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Weight (lbs)</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        min="0"
                        step="0.5"
                        {...field}
                        onChange={e => field.onChange(Number(e.target.value))}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              
              <FormField
                control={form.control}
                name="restTime"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Rest Time (seconds)</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        min="0"
                        max="600"
                        {...field}
                        onChange={e => field.onChange(e.target.value ? Number(e.target.value) : null)}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
            
            <FormField
              control={form.control}
              name="rating"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rating (1-10)</FormLabel>
                  <div className="flex items-center space-x-4">
                    <FormControl>
                      <Slider
                        min={1}
                        max={10}
                        step={1}
                        value={[field.value]}
                        onValueChange={(values) => field.onChange(values[0])}
                        className="flex-1"
                      />
                    </FormControl>
                    <span className="text-lg font-semibold w-8 text-center">
                      {field.value}
                    </span>
                  </div>
                  <FormMessage />
                </FormItem>
              )}
            />
            
            <DialogFooter>
              <Button type="button" variant="outline" onClick={handleClose}>
                Cancel
              </Button>
              <Button type="submit">
                {editEntry ? 'Update' : 'Add'} Workout
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
};

export { AddWorkoutLogModal };
export default AddWorkoutLogModal;
